<template>
	<div>
		<div class="contacts--list__item">
			<label class="check">
				<input type="checkbox">
				<div class="check-mark"></div>
			</label>
			<div class="contacts__info">
				<div class="contacts__img">
					<img
						v-if="alumnus.user.avatar"
						:src="alumnus.user.avatar"
						alt="Profile picture">
					<img v-else src="../../assets/img/pic.png" alt="Profile picture">
				</div>
				<div class="info__primary">
					<p class="info__name">{{ fullName }}</p>
					<p class="info__email">{{ alumnus.user.email }}</p>
				</div>
			</div>
			<p>
				<span :class="isActive">
                    {{ alumnus.user.active === 1 ? 'Active' : 'Suspended'}}
				</span>
			</p>
			<p>{{ alumnus.user.phone }}</p>
			<p class="info__address">{{ this.alumnus.user.email }}</p>
			<p>
				<router-link
					:to="{ name: 'alumni-profile',
					params: { alumnus: alumnus, id: alumnus.user.id }}"
					class="btn btn__primary">
					View Profile
				</router-link>
			</p>
		</div>
	</div>
</template>

<script>

export default {
	name: 'AlumniItem',
	props: {
		alumnus: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	data() {
		return {};
	},
	computed: {
		fullName() {
			return `${this.alumnus.user.fname} ${this.alumnus.user.lname}`;
		},
		isActive() {
			return [
				this.alumnus.user.active === 1
					? 'badge badge-pill badge-success'
					: 'badge badge-pill badge-danger'
			];
		}
	}
};
</script>
