<template>
	<div>
		<template v-if="loadingAlumni">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Alumni</h3>
				</div>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Basic info</p>
						<p>Status</p>
						<p>phone</p>
						<p>Email</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<template v-if="alumniLoaded && alumni.length">
							<AlumniItem
								v-for="alumnus in alumni"
								:key="alumnus.id"
								:alumnus="alumnus"/>
						</template>
						<template v-else-if="alumniLoaded">
							<p>No Alumni found</p>
						</template>
						<template v-else>
							<p>An error occurred while loading alumni, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
		<Pagination
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit" />
	</div>
</template>


<script>
import Loader from '../../components/Loader';
import Pagination from '../../components/Pagination';
import AlumniItem from './AlumniItem';
import { getAlumni } from '../../utils/api/alumni';

export default {
	name: 'Index',
	data() {
		return {
			loadingAlumni: true,
			alumniLoaded: false,
			alumni: {},
			limit: 10,
			offset: 0,
			total: 0,
			baseUrl: 'alumni'
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getAlumni();
	},
	components: {
		Loader,
		Pagination,
		AlumniItem
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getAlumni();
		}
	},
	methods: {
		getAlumni() {
			this.loadingAlumni = true;
			getAlumni(this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.alumni = response.data.data;
						this.total = response.data.total;
						this.alumniLoaded = true;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.alumniLoaded = false;
					this.loadingAlumni = false;
					return error;
				})
				.finally(() => {
					this.loadingAlumni = false;
				});
			return true;
		}
	}
};
</script>

<style lang="scss">
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contacts--list__item {
    display: grid;
    grid-template-columns: 35px 2fr 1fr 1fr 1fr 150px;
}
</style>
